<template>
  <div class="login">
    {{ code }}
    <mt-header :title="title" class="boder-header">
      <mt-button icon="back" slot="left" @click="$router.go(-1)"></mt-button>
      <!-- <mt-button slot="right" @click="changePanel">{{rightTitle}}</mt-button> -->
    </mt-header>

    <div class="login-quick" v-show="quickVisable">
      <form class="login-form" @submit="checkForm" action="" v-model="form">
        <mt-field placeholder="请输入手机号码" type="tel" v-model="form.phoneNumber" class="phone"></mt-field>
        <form-captcha :phoneNumber="form.phoneNumber" :captcha.sync="form.captcha"></form-captcha>
        <form-verification :phoneNumber="form.phoneNumber" :captcha="form.captcha" :verification.sync="form.verification"></form-verification>

        <div class="mint-cell-wrapper btn-module">
          <div class="btn-margin">
            <mt-button class="common-btn">登录</mt-button>
          </div>
          <div class="login-checkbox">
            <mt-checklist
              v-model="form.agreeValue"
              :options="options">
            </mt-checklist>
            <span>注册即表示阅读并同意《<a @click="showAgreement"> 财喵万卡注册服务协议</a>》</span>
          </div>
        </div>

      </form>
    </div>
  </div>
</template>

<script>
import { mapActions, mapMutations, mapState } from 'vuex'
import FormCaptcha from '@/components/login/FormCaptcha'
import FormVerification from '@/components/login/FormVerification'
import valid from '@/modules/valid'

export default {
  name: 'login',
  components: { FormCaptcha, FormVerification },
  data () {
    return {
      title: '手机号快捷登录',
      rightTitle: '密码登录',
      // popupVisible: false,
      quickVisable: true,
      options: [
        {
          label: ' ',
          value: true
        }
      ],
      code: '',
      form: {
        phoneNumber: localStorage.getItem('caiPhone'),
        captcha: '',
        verification: '',
        agreeValue: [true]
      }
    }
  },
  computed: {
    ...mapState({
      loginThirdType: state => state.accountInfo.loginThirdType,
      thirdType: state => state.accountInfo.thirdType,
      tempToken: state => state.accountInfo.tempToken
    })
  },
  created () {
    if (this.$route.query.code) {
      this.code = this.$route.query.code
    }
    this.SETLOGINVALUE({
      captchaShow: false,
      sendShow: true,
      btnDisable: false
    })
  },
  mounted () {
    // document.title = '手机号快捷登录'
  },
  methods: {
    ...mapActions([
      'getUserInfo' // 将 `this.incrementBy(amount)` 映射为 `this.$store.dispatch('incrementBy', amount)`
    ]),

    ...mapMutations([
      'SETLOGINVALUE', 'SETLOGINSTATE'
    ]),

    changePanel () {
      this.quickVisable = !this.quickVisable
      if (!this.quickVisable) {
        this.title = '密码登录'
        this.rightTitle = '快捷登录'
      } else {
        this.title = '手机号快捷登录'
        this.rightTitle = '密码登录'
      }
    },

    // 向后台发出登录请求
    toLogin: function (form) {
      this.$http.toLogin(form).then((res) => {
        if (res) {
          this.SETLOGINSTATE({loginState: true})
          this.getUserInfo()
          var redirectUrl = this.$route.query.redirected
          if (redirectUrl) {
            setTimeout(() => {
              this.$router.push({path: redirectUrl})
            }, 300)
          } else {
            this.$router.go(-1)
          }
        }
      }).catch(err => {
        console.log(err)
      })
    },

    checkForm: function (e) {
      localStorage.setItem('caiPhone', this.form.phoneNumber || '')
      var phone = valid.validatePhoneNumber(this.form.phoneNumber)

      if (phone === '') {
        if (!this.form.captcha) {
          this.$toast('图形验证码不能为空')
        } else if (!this.form.verification) {
          this.$toast('短信验证码不能为空')
        } else if (this.form.agreeValue.length === 0) {
          this.$toast('请同意财喵万卡注册服务协议')
        } else {
          let form = {
            phone: this.form.phoneNumber,
            captcha_code: this.form.verification,
            img_code: this.form.captcha,
            type: 4
            // channel: 'wapzc',
            // thirdType: this.thirdType,
            // tempToken: this.tempToken
          }

          this.toLogin(form)
        }
      } else {
        this.$toast(phone)
      }

      e.preventDefault()
    },

    showAgreement () {
      // this.popupVisible = true
      this.$router.push('/user/agreement')
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss">

.login {
  .login-checkbox{
    margin: 0px 12px 55px;
    display: flex;
    font-size: 12px;
    line-height: 21px;
    color: #A4A4A4;
    text-align: center;
    white-space: nowrap;
    & a{
      color: #FF683F;
      text-decoration: none;
    }
    & span{
      margin-top: 6px;
    }
    & .mint-checklist{
      margin-top: -2px;
    }
    & .mint-checklist-title{
      margin: 0;
    }
    & .mint-checklist-label{
      padding: 0;
    }
  }

  .agreement-popup{
    width: 100%;
    height: 100%;
  }

  .common-btn {
    background: #FF683F;
    color: #fff;
    border-color: #FF683F;
    box-shadow: none;
    height: 44px;
    font-size: 15px;
    padding: 0 15px;
    &.blue {
      background-color: #4477dd;
    }
  }

  .btn-module {
    display: block;
    & .btn-margin {
      margin: 50px 20px 15px;
    }
    & .common-btn {
      display: block;
      width: 100%;
    }
    & .mint-cell-wrapper {
      width: 22px;
    }
  }

  .mint-checkbox-core {
    width: 15px;
    height: 15px;
    border-radius: 2px;
    top: 4px;
    &:after {
      top: 0;
      left: 4px;
      content: " ";
    }
  }
  .mint-checkbox-input:checked + .mint-checkbox-core {
    background-color: #FF683F;
    border-color: #FF683F;
    border-radius: 2px;
  }
  .mint-checklist {
    width: 20px;
  }

  .login-form {
    margin: 10px 8%;
    & > .mint-cell {
      margin-top: 30px;
    }
    & .mint-cell-wrapper {
      padding: 0;
    }
    & .mint-cell:last-child {
      background-image: none;
    }
    .mint-field.is-nolabel .mint-cell-title {
      position: relative;
      display: inherit;
      width: 30px;
      font-family: "iconfont" !important;
      font-size: 18px;
      margin: 0 10px;
      color: #100c29;
      &:after {
        position: absolute;
        right: 0;
        top: 6px;
        width: 1px;
        height: 15px;
        content: "";
        background-color: #e8e8e8;
      }
    }
    .phone .mint-cell-title {
      &:before {
        content: "";
        width: 26px;
        height: 26px;
        display: block;
        background: url(../../assets/images/phone@2x.png);
        background-size: 100% auto;
        background-repeat: no-repeat;
      }
    }
    .imgCaptcha .mint-cell-title {
      &:before {
        content: '';
        width: 26px;
        height: 26px;
        display: block;
        background: url(../../assets/images/pic_code@2x.png);
        background-size: 100% auto;
        background-repeat: no-repeat;
      }
    }
    .msgCaptcha .mint-cell-title {
      &:before {
        content: "";
        width: 26px;
        height: 26px;
        display: block;
        background: url(../../assets/images/message_code@2x.png);
        background-size: 100% auto;
        background-repeat: no-repeat;
      }
    }
  }
}
</style>



