/**
 * 表单验证方法
 */

const valid = {}

valid.validatePhoneNumber = function (value) {
  if (!value || value === '') {
    return '请输入手机号码'
  }
  var phonrReg = /^1\d{10}$/
  if (!phonrReg.test(value)) {
    return '请输入正确的手机号码'
  } else {
    return ''
  }
}

export default valid



